import React,{ useState, useEffect } from "react";
import { Navbar, Nav, Container, Form, Button, Col } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../actions/userActions";
import searchicon from "../images/search.png"

function Headers() {
  const dispatch = useDispatch();

  const [searchContent, setSearchContent] = useState(" ");

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const logoutHandler = () => {
    dispatch(logout());
  };

  return (
    <header>
      <Navbar bg="primary" variant="dark" expand="lg" collapseOnSelect>
        <Container>
          <LinkContainer to="/">
            <Navbar.Brand>Social Distribution</Navbar.Brand>
          </LinkContainer>

          <Form.Label htmlFor="inlineFormInput" visuallyHidden>
                        PostContent
          </Form.Label>
                    <Form.Control
                        style={{width:"60%", marginLeft:"4%"}}
                        id="inlineFormInput"
                        placeholder="Search a post"
                        onChange={(e) => setSearchContent(e.target.value)}
                    />
          <Col xs="auto">
            <LinkContainer to={{pathname:"/searchresult", searchContent}} style={{width:"40%", backgroundColor:"orange"}}>
              <Button>
                <img src={searchicon} style={{width:"80%" }}></img>
              </Button>
            </LinkContainer>
          </Col>


          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">              
              <LinkContainer to="/profile">
                <Nav.Link>Profile</Nav.Link>
              </LinkContainer>
              {userInfo ? (
                <Nav.Link onClick={logoutHandler}>Logout</Nav.Link>
              ) : (
                <LinkContainer to="/login">
                  <Nav.Link>Login</Nav.Link>
                </LinkContainer>
              )}
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </header>
  );
}

export default Headers;
